<!--
  都是在挂载点下的设备操作
-->
<template>
  <div>
    <div>
      <table>

      </table>

    </div>
    请输入id： <input v-model="id"/>
    <button @click="getUser">查询</button>
    <textarea v-model="u_context"></textarea>
    <!--    <select v-model="selectNode">-->
    <!--      <option v-for="name in listInfos">{{name['node-id']}}</option>-->
    <!--    </select><br>-->
    <el-button type="primary" @click="getInfo">节点列表</el-button>
    <el-button type="danger" @click="deleteNode">删除节点</el-button>
    <el-select
        v-model="selectNode"
        placeholder="请选择">
      <el-option v-for="(nodeName,index) in listInfos" :key="index"
                 :value="nodeName['node-id']">
      </el-option>

    </el-select>
    <el-input label="端口名称" v-model="ptpName"></el-input>
    <el-button @click="getMe">设备信息</el-button>
    <el-button @click="getNtp">时间服务</el-button>
    <el-button @click="getPtps">端口信息</el-button>
    <el-button @click="getSession">会话信息</el-button>
    <el-button @click="statistics">统计信息</el-button>
    <el-button @click="getEqs">单板信息</el-button>
    <textarea v-model="all_cache"></textarea>
    <ODUPTP :node="selectNode" :ptp="ptpName"/>

    <el-popover
        placement="right"
        width="400"
        trigger="click">
      <el-table :data="schema">
        <el-table-column width="150" property="identifier" label="模块"></el-table-column>
        <el-table-column width="150" property="version" label="日期"></el-table-column>
      </el-table>
      <el-button slot="reference" @click="getSchemas">模式列表</el-button>
    </el-popover>

    <el-col>
      <div :style='{height: "800px", width:"1300px"}' id="myChart" ref="myChart"></div>
      <div>
        <el-button type="success" @click="createDevm">创建设备</el-button>
        <el-button type="primary" size="small" @click="getInfo">刷新</el-button>
        <el-input label="节点名称" v-model="delName"></el-input>
        <el-table
            :data="listInfos"
            style="width: 100%"
            :row-class-name="tableRowClassName"
            @row-contextmenu.native.prevent="onContextmenu"
        >

          <el-table-column
              prop="node-id"
              label="网元名称"
              width="180">
          </el-table-column>

          <el-table-column
              prop="netconf-node-topology:connection-status"
              label="连接状态"
              width="180">

          </el-table-column>
          <el-table-column
              prop="netconf-node-topology:host"
              label="设备地址">
          </el-table-column>
          <el-table-column
              prop="netconf-node-topology:port"
              label="设备端口">
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <!--            <el-popconfirm
                              confirm-button-text='好的'
                              cancel-button-text='不用了'
                              icon="el-icon-info"
                              icon-color="red"
                              title="确定删除该节点吗？"
                              @onConfirm="handleDelete(scope.$index, scope.row)"

                          >
                            <el-button slot="reference" size="mini" type="danger">删除</el-button>
                          </el-popconfirm>-->
              <el-popover
                  placement="top"
                  width="160"
                  v-model="visible"
                  slot="reference"
              >
                <p>确定删除当前行节点内容吗？</p>
                <div style="text-align: right; margin: 0">
                  <el-button size="mini" type="text" @click="visible = false">取消</el-button>
                  <el-button type="primary" size="mini" @click="handleDelete(scope.$index, scope.row)">确定</el-button>
                </div>
                <el-button
                    size="mini"
                    type="danger"
                    slot="reference">删除
                </el-button>
                <el-button
                    size="mini"
                    slot="reference"
                    @click="handleEdit(scope.$index, scope.row)">编辑
                </el-button>

              </el-popover>

            </template>
          </el-table-column>
        </el-table>
      </div>

    </el-col>
    <el-dialog :title="title" :visible.sync="dialogFormVisible">
      <devm-info :name="selectName"></devm-info>
    </el-dialog>


  </div>

</template>

<script>
import axios from "axios";
import Ip from "@/components/test/Ip";
import DevmInfo from "@/components/DevmInfo";
import ETH from "../XC/Eth";
import ODUPTP from "../property/ptp/OduPtp";
import *as echarts from 'echarts';
import chinaMap from "@/assets/json/china.json";

export default {
  components: {
    // eslint-disable-next-line vue/no-unused-components
    Ip,
    ETH,
    DevmInfo,
    ODUPTP,
  },
  data() {
    return {
      all_cache:'',
      id:'',
      u_context:'',
      ptpName:'',
      schema: [
        {
          "identifier": "",
          "version": "",
          "format": "",
          "namespace": "",
          "location": [
            "NETCONF"
          ]
        },
      ],
      title: '',
      dialogFormVisible: false,
      delName: '',
      selectName: '',
      selectNode: '',
      visible: false,
      flag: true,
      selectRows: [],
      selectTotal: 0,
      listInfos: [{
        'node-id': '',
        'netconf-node-topology:connection-status': '',
        'netconf-node-topology:port': '',
        'netconf-node-topology:host': '',
      }],
      rules: {
        name: [
          {required: true, message: '请输入网元名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur'}
        ],
      }
    };
  },
  mounted() {
    this.drawLine();

    axios.get('/list/devices').then(
        response => {
          console.log("请求成功", response.data);
          this.listInfos = response.data;
        },
        error => {
          console.log("失败！", error.message)
        }
    )
  },
  methods: {
    show(event) {
      // this.$contextmenu({
      //   items: [
      //     {
      //       label: "设备信息",
      //       onClick: () => {
      //         this.message = "返回(B)";
      //         console.log("返回(B)");
      //       }
      //     },
      //     { label: "前进(F)", disabled: true },
      //     { label: "重新加载(R)", divided: true, icon: "el-icon-refresh" },
      //     { label: "另存为(A)..." },
      //     { label: "打印(P)...", icon: "el-icon-printer" },
      //     { label: "投射(C)...", divided: true },
      //     {
      //       label: "使用网页翻译(T)",
      //       divided: true,
      //       minWidth: 0,
      //       children: [{ label: "翻译成简体中文" }, { label: "翻译成繁体中文" }]
      //     },
      //     {
      //       label: "截取网页(R)",
      //       minWidth: 0,
      //       children: [
      //         {
      //           label: "截取可视化区域",
      //           onClick: () => {
      //             this.message = "截取可视化区域";
      //             console.log("截取可视化区域");
      //           }
      //         },
      //         { label: "截取全屏" }
      //       ]
      //     },
      //     { label: "查看网页源代码(V)", icon: "el-icon-view" },
      //     { label: "检查(N)" }
      //   ],
      //   event, // 鼠标事件信息
      //   customClass: "custom-class", // 自定义菜单 class
      //   zIndex: 3, // 菜单样式 z-index
      //   minWidth: 230 // 主菜单最小宽度
      // });
      console.log("触发事件")
    },
    //右键点击
    rightClick(MouseEvent, object, Node, element) { // 鼠标右击触发事件
      this.menuVisible = false // 先把模态框关死，目的是 第二次或者第n次右键鼠标的时候 它默认的是true
      this.menuVisible = true  // 显示模态窗口，跳出自定义菜单栏
      var menu = document.querySelector('#menu')
      document.addEventListener('click', this.foo) // 给整个document添加监听鼠标事件，点击任何位置执行foo方法
      menu.style.display = "block";
      menu.style.left = MouseEvent.clientX - 0 + 'px'
      menu.style.top = MouseEvent.clientY - 80 + 'px'
    },
    drawLine() {
      echarts.registerMap("china", {geoJSON: chinaMap});
      let charts = echarts.init(this.$refs.myChart);
      charts.setOption({
        backgroundColor: "#c5e8f6",
        title: {
          text: "山东华辰泰尔设备管理图",
          subtext: "",
          x: "center"
        },
        tooltip: {
          trigger: "item"
        },

        //左侧小导航图标
        visualMap: {
          min: 0,
          max: 600,
          text: ["高", "低"],
          inRange: {
            color: ["#D8FAFE", "#006EDD"]//此处是设置颜色过渡
          }
        },

        //配置属性
        series: [
          {
            name: "报名人数",
            type: "map",
            mapType: "china", // ---此处是中国地图样式-------需要注意：省份中应使用汉字即如 ‘mapType:"河南"’
            roam: true,
            label: {
              normal: {
                show: true //省份名称----true，展示每个省份的名称,false 隐藏
              },
              emphasis: {
                show: false
              }
            },
            data: [//这是数据，500以内的随机数
              {name: "北京", value: "100"},
              {name: "天津", value: Math.round(Math.random() * 500)},
              {name: "上海", value: Math.round(Math.random() * 500)},
              {name: "重庆", value: Math.round(Math.random() * 500)},
              {name: "河北", value: Math.round(Math.random() * 500)},
              {name: "河南", value: Math.round(Math.random() * 500)},
              {name: "云南", value: Math.round(Math.random() * 500)},
              {name: "辽宁", value: Math.round(Math.random() * 500)},
              {name: "黑龙江", value: Math.round(Math.random() * 500)},
              {name: "湖南", value: Math.round(Math.random() * 500)},
              {name: "安徽", value: Math.round(Math.random() * 500)},
              {name: "山东", value: Math.round(Math.random() * 500)},
              {name: "新疆", value: Math.round(Math.random() * 500)},
              {name: "江苏", value: Math.round(Math.random() * 500)},
              {name: "浙江", value: Math.round(Math.random() * 500)},
              {name: "江西", value: Math.round(Math.random() * 500)},
              {name: "湖北", value: Math.round(Math.random() * 500)},
              {name: "广西", value: Math.round(Math.random() * 500)},
              {name: "甘肃", value: Math.round(Math.random() * 500)},
              {name: "山西", value: Math.round(Math.random() * 500)},
              {name: "内蒙古", value: Math.round(Math.random() * 500)},
              {name: "陕西", value: Math.round(Math.random() * 500)},
              {name: "吉林", value: Math.round(Math.random() * 500)},
              {name: "福建", value: Math.round(Math.random() * 500)},
              {name: "贵州", value: Math.round(Math.random() * 500)},
              {name: "广东", value: Math.round(Math.random() * 500)},
              {name: "青海", value: Math.round(Math.random() * 500)},
              {name: "西藏", value: Math.round(Math.random() * 500)},
              {name: "四川", value: Math.round(Math.random() * 500)},
              {name: "宁夏", value: Math.round(Math.random() * 500)},
              {name: "海南", value: Math.round(Math.random() * 500)},
              {name: "台湾", value: Math.round(Math.random() * 500)},
              {name: "香港", value: Math.round(Math.random() * 500)},
              {name: "澳门", value: Math.round(Math.random() * 500)}
            ] //数据
          }
        ]
      }),
          window.addEventListener("resize", function () {
            charts.resize();
          })
    },
    onContextmenu(event) {
      console.log("点击成功")
      this.$contextmenu({
        items: [
          {
            label: "t返回(B)",
            onClick: () => {
              this.message = "返回(B)";
              console.log("返回(B)");
            }
          },
          {label: "t前进(F)", disabled: true},
          {label: "t重新加载(R)", divided: true, icon: "el-icon-refresh"},
          {label: "t另存为(A)..."},
          {label: "t打印(P)...", icon: "el-icon-printer"},
          {label: "t投射(C)...", divided: true},
          {
            label: "使用网页翻译(T)",
            divided: true,
            minWidth: 0,
            children: [{label: "翻译成简体中文"}, {label: "翻译成繁体中文"}]
          },
          {
            label: "截取网页(R)",
            minWidth: 0,
            children: [
              {
                label: "截取可视化区域",
                onClick: () => {
                  this.message = "截取可视化区域";
                  console.log("截取可视化区域");
                }
              },
              {label: "截取全屏"}
            ]
          },
          {label: "查看网页源代码(V)", icon: "el-icon-view"},
          {label: "检查(N)"}
        ],
        event, // 鼠标事件信息
        customClass: "custom-class", // 自定义菜单 class
        zIndex: 3, // 菜单样式 z-index
        minWidth: 230 // 主菜单最小宽度
      });
      return false;
    },
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          const {data: res} = await this.$http.post("/create/node", this.node);
          if (res === 200) {
            this.$message.success("已有设备，更新数据成功");
            // this.getInfo();
          } else if (res === 201) {
            this.$message.success("新设备创建成功");
            // this.getInfo();
          } else {
            this.$message.error("创建失败");
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    showPassword() {
      var isPassword = !this.data.isPassword;
      this.setData({
        isPassword: isPassword
      })
    },
    getInfo() {
      axios.get('/list/nodes').then(
          response => {
            console.log("请求成功", response.data);
            this.listInfos = response.data;
          },
          error => {
            console.log("失败！", error.message)
          }
      )
    },
    getUser() {
      axios.get("/meInfos", {
        params:{'id': this.id}
      }).then(
          response => {
            console.log("请求成功", response.data);
            this.u_context = response.data;
          },
          error => {
            console.log("id:",this.id)
            console.log("失败！", error.message)
          }
      )
    },
    getPtp() {
      axios.get("/node/ptps/ptp", {
        params:{'name': this.ptpName}
      }).then(
          response => {
            console.log("请求成功", response.data);
            this.u_context = response.data;
          },
          error => {
            console.log("name:",this.ptpName)
            console.log("失败！", error.message)
          }
      )
    },
    async getMe() {
      const {data: res} = await this.$http.post("/node/me", this.selectNode);
      console.log(res);
      this.all_cache = res;

    },
    async getEqs() {
      const {data: res} = await this.$http.post("/node/eqs", this.selectNode);
      console.log(res);
      this.all_cache = res;

    },
    async sendJson() {
      console.log(this.eth);
      const {data: res} = await this.$http.post("/test/rpc", this.eth);
      console.log("返回结果", res);
      this.dialogFormVisible = false;

    },
    async statistics() {
      const {data: res} = await this.$http.post("/node/statistics", this.selectNode);
      console.log(res);
    },
    async getSession() {
      const {data: res} = await this.$http.post("/node/session", this.selectNode);
      console.log(res);
    },
    async getPtps() {
      const {data: res} = await this.$http.post("/node/ptps", this.selectNode);
      console.log(res);
    },
    async getNtp() {
      const {data: res} = await this.$http.post("/node/ntp", this.selectNode);
      console.log(res);
    },
    async getSchemas() {
      const {data: res} = await this.$http.post("/node/schemas", this.selectNode);
      this.schema = res;
      console.log(res);
    },
    async deleteNode() {
      axios.post("delete/node", this.selectNode).then(
          response => {
            console.log("请求成功", response.data);
          },
          error => {
            console.log("失败！", error.message)
          }
      )
      // const {data: res} = await this.$http.post("/delete/node", this.selectNode);
      // console.log(res);
    },
    async handleEdit(index, row) {
      this.dialogFormVisible = true;
      this.title = "修改设备信息";
      this.selectName = row['node-id'];

      console.log("准备发送的名字", this.selectName)
      // console.log(index);
      // console.log(row);
    },
    async handleDelete(index, row) {
      console.log(row);
      console.log(row['node-id']);
      this.delName = row['node-id'];
      const {data: res} = await this.$http.post("/delete/node", this.delName);
      console.log("返回状态：", res)
      this.visible = false;
    },

    createDevm() {
      this.dialogFormVisible = true;
      this.title = "创建设备";
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    tableRowClassName({row, rowIndex}) {
      // if (rowIndex){
      //   return 'other-row';
      // }
      if (row['netconf-node-topology:connection-status'] === 'connected') {

        return 'success-row';
      } else {
        return 'warning-row';
      }
    },
    aa(name) {
      var str = ''
      switch (name) {
        case 'connecting':
          console.log("get" +
              "dao" +
              "l")
          break;
        case 'connected':
          console.log('connected')
          break;
        default:
          str = name;
      }
      return str;
    },
    handleSelection(selection, row) {
      this.selectRows = row;
      this.selectTotal = selection.length;
      console.log(row);
      console.log(selection.length);
    },
  },


}
</script>

<style scoped>
#test {
  /*background: #a3a6aa;*/
  width: 800px;
  height: auto;
}

el-switch {
  padding-left: 5px;
}

.el-table .warning-row {
  background: #d08787;
}

.el-table .success-row {
  background: #c6e0bc;
}

.el-table--enable-row-hover .el-table__body :hover {

  background-color: #55d73b;

}

.el-table .other-row {
  background: #2d8daa;
}

