<script>
import DevmInfo from "@/components/DevmInfo";
import SetPerformances from "@/components/Performances/SetPerformances";
import axios from "axios";
import LeftTree from "@/views/layout/HcLeftTree";
import Me from "@/components/property/me/Me";
import TipWindows from "@/components/Tip/TipWindows";
import MatchConfig from "@/components/Tip/MatchConfig";

export default {
  components: {
    MatchConfig,
    TipWindows,
    Me,
    DevmInfo,
    LeftTree,
    SetPerformances,
  },
  data() {
    return {

      editableTabsValue: '2',
      editableTabs: [{
        title: 'Tab 1',
        name: '1',
        content: 'Tab 1 content'
      }, {
        title: 'Tab 2',
        name: '2',
        content: 'Tab 2 content'
      }],
      tabIndex: 2,

      //右侧设备树所点击的层级结构
      nodeLevel: 0,

      enableMe: false,
      enableEq: false,
      enableTp: false,

      matchConfig: false,
      syncAlarm: false,
      syncHistory: false,
      //判断是否 选中点击了节点
      accessStatus: true,
      meFormVisible: false,
      createDevmFormVisible: false,
      configDevmFormVisible: false,
      configTcasFormVisible: false,

      nodeName: "",
      newName: '',
      menuMode: 'horizontal',
      activeIndex: "1",
      activeIndex2: "/oos/ne_center/demo",
      navList: [
        {name: "/", navItem: "首页"},
        {name: "/test1", navItem: "发现项目"},
        {name: "/test2", navItem: "社区动态"},
      ],
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    getMe() {
      this.meFormVisible = true;
    },
    createDevm() {
      this.createDevmFormVisible = true;
    },
    configDevm() {
      this.isAccess();
      if (this.accessStatus === true) {
        this.configDevmFormVisible = true;
      }
    },
    configTcas() {
      this.isAccess();
      if (this.accessStatus === true) {
        this.configTcasFormVisible = true;
      }
    },
    isAccess() {
      if (this.nodeName === "") {
        this.accessStatus = false;
        this.$message.warning("请选择节点后再操作");
      } else {
        this.accessStatus = true;
      }
      console.log(this.accessStatus);
    },
    deleteDevm() {
      this.isAccess();
      if (this.accessStatus === true) {
        this.$confirm("此操作将永久删除该网元节点, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        })
            .then(() => {
              this.deleteNode();
              /*        this.$message({
                      type: 'success',
                      message: '删除成功!'
                    });*/
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消删除",
              });
            });
      }
    },
    deleteNode() {
      if (this.nodeName !== "") {
        axios.delete("/node/" + this.nodeName).then(
            (response) => {
              console.log("请求成功", response.data);
              this.$message.success("删除设备： " + this.nodeName + "成功！！！");
              //删除的节点发送出去
              this.$bus.$emit("delete-node", this.nodeName);
              this.nodeName = "";
            },
            (error) => {
              console.log("失败！", error.message);
              this.$message.error(error.message);
            }
        );
      }
    },

    isEnable(level) {
      switch (level) {
        case 1:
        case 2:
          this.enableMe = true;
          this.enableEq = false;
          this.enableTp = false;
          break;
        case 3:
          this.enableEq = true;
          this.enableMe = false;
          this.enableTp = false;
          break;
        case 4:
          this.enableTp = true;
          this.enableMe = false;
          this.enableEq = false;
          break;
      }
    },
  },
  watch: {
    nodeName: {
      immediate: true, //初始化时让handler调用一下
      //handler什么时候调用？当isHot发生改变时。
      handler(newValue, oldValue) {
        this.newName = '搜索设备配置信息：' + newValue;
        console.log('isHot被修改了', newValue, oldValue)
      }
    }
  },
  mounted() {
    this.$bus.$on("update:visible", (data) => {
      console.log("    this.$on(\"update:visible\",this)\n")
      this.matchConfig = data;

    });
    this.$bus.$on("node-name", (data) => {
      this.nodeName = data;
    });
    this.$bus.$on("level", (data) => {
      this.nodeLevel = data;
      console.log('我是标题组件，我收到了右侧设备树点击的层级信息 ： ', this.nodeLevel);
      this.isEnable(this.nodeLevel);
    });
    this.isAccess();
  },
};
</script>

