<template>

  <div>
    <el-tabs @tab-click="handleClick" active-name="1">
      <el-tab-pane label="基本属性" name="1">
        <div v-show="false">
          <el-button style="float: right; padding: 3px 0" type="text" @click="getPtps">刷新</el-button>
          <el-table :data="ptps">
            <el-table-column
                type="index"
                width="50">
            </el-table-column>
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand expanded_ptp_class">
                  <el-form-item label="包含的 CTP">
                    <span v-for="(i,key) in props.row.ctp" :key="key">{{ i }} <el-divider
                        direction="vertical"></el-divider></span>
                  </el-form-item>
                  <el-form-item label="环回类型">
                    <span>{{ props.row['loop-back'] }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column property="name" label="名称" sortable width="300px"></el-table-column>
            <!--          <el-table-column property="h2.h3" label="测试" sortable></el-table-column>-->
            <el-table-column width="auto" property="statePac.adminState" label="管理配置状态"></el-table-column>
            <el-table-column width="auto" property="statePac.operationalState" label="工作状态"></el-table-column>
            <el-table-column width="auto" property="interface-type" label="接口类型"></el-table-column>
            <el-table-column width="auto" property="laser-status" label="激光器状态"></el-table-column>
            <el-table-column width="auto" property="loop-back" label="环回类型"></el-table-column>
            <el-table-column width="150" property="layer-protocol-name" label="协议层名称"></el-table-column>
            <!--          <el-table-column width="150" property="ctp" label="ctp" sortable></el-table-column>-->
          </el-table>
        </div>
        <div style="width: 400px">
          <el-form :model="ptp" ref="ptp" label-width="100px" class="config_form">
            <div>
              <el-form-item label="名称">
                <el-input v-model="ptp.name" readonly></el-input>
              </el-form-item>
              <el-form-item label="工作状态">
                <el-input v-model="ptp.statePac.operationalState" readonly></el-input>
              </el-form-item>
              <el-form-item label="接口类型">
                <el-input v-model="ptp['interface-type']" readonly></el-input>
              </el-form-item>

            </div>

            <el-form-item label="管理状态" pprop="statePac.adminState" style="margin-top: 3px">
              <el-select v-model="ptp.statePac.adminState">
                <el-option value="使能"></el-option>
                <el-option value="禁止"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="激光器状态" prop="mac-address">
              <el-select v-model="ptp['laser-status']">
                <el-option value="使能"></el-option>
                <el-option value="禁止"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="环回类型">
              <el-select v-model="ptp['loop-back']">
                <el-option value="内环回"></el-option>
                <el-option value="外环回"></el-option>
                <el-option value="无环回"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="协议类型">
              <el-select v-model="ptp['layer-protocol-name']">
                <el-option value="ETH"></el-option>
                <el-option value="SDH"></el-option>
                <el-option value="ODU"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')" style="float: right">确定</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>
      <el-tab-pane label="ETH" name="2">
        <EthPtp/>
      </el-tab-pane>
      <el-tab-pane label="ODU" name="3">
        <OduPtp/>
      </el-tab-pane>
      <el-tab-pane label="SDH" name="4">
        <SdhPtp/>
      </el-tab-pane>
    </el-tabs>
  </div>

</template>

<script>
import axios from "axios";
import EthPtp from "@/components/Eth/EthPtp";
import OduPtp from "@/components/property/ptp/OduPtp";
import SdhPtp from "@/components/sdh/SdhPtp";

export default {
  name: "PTP",
  components: {
    EthPtp,
    OduPtp,
    SdhPtp
  },
  data() {
    return {
      nodeName: '',
      ptps: [{
        "interface-type": "UNI",
        "laser-status": "laserOn",
        "name": "PTP=/shelf=1/slot=1/subslot=1/port=8",
        "layer-protocol-name": "ETH",
        "acc-eth:eth-ptp-pac": {
          "mac-address": "00:00:00:00:00:00",
          "current-working-mode": "auto",
          "pause-control": true,
          "lldp-enable": false,
          "current-mtu": 1518
        },
        "loop-back": "内环回",
        statePac: {
          "adminState": "enabled",
          "operationalState": 'down',
        },
        "ctp": ["CTP=8", "CTP=10"],
        "optical-power-pac": {}
      }],
      ptp: {
        "name": "端口1",
        "interface-type": "UNI",
        "laser-status": "禁止",
        "layer-protocol-name": "ETH",
        "loop-back": "内环回",
        statePac: {
          "adminState": "使能",
          "operationalState": 'down',
        },
      }
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    getPtps() {
      axios.get("/node/" + this.nodeName + "/ptps").then(
          response => {
            console.log("请求成功", response.data);
            this.ptps = response.data;
            // response.data;
          },
          error => {
            console.log("失败！", error.message)
          }
      )
    },
    submitForm(formName) {
      console.log(formName)
    }
  },
  mounted() {
    this.$bus.$on('node-name', (data) => {
      console.log('PTP - node : ', data);
      this.nodeName = data;
    })
    this.$bus.$on('ptp-name', (data) => {
      console.log('ptp基本信息收到了消息', data)
      this.ptp.name = data;
    })
  }
}
</script>

<style scoped>
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>