<template>
  <div class="config_div">
    <el-header style="background-color: #5fbfb0">ODU CTP XC 配置</el-header>
    <el-form :model="oduCtp" :rules="rules" ref="ruleForm" label-width="100px" class="config_form">
      <el-form-item label="交叉名称" prop="name">
        <el-input v-model="oduCtp.name"></el-input>
      </el-form-item>
      <el-form-item label="源CTP" prop="source">
        <el-select v-model="oduCtp.source" clearable placeholder="请选择源CTP">
          <el-option label="CTP=1/2" value="shanghai"></el-option>
          <el-option label="CTP=2/3" value="beijing"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="目的CTP" prop="dest">
        <el-select v-model="oduCtp.dest" clearable placeholder="请选择目的CTP">
          <el-option label="CTP=1/2" value="shanghai"></el-option>
          <el-option label="CTP=2/3" value="beijing"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">确认</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  name:'OduCtpXCConfig',
  data() {
    return {
      oduCtp: {
        name: '',
        source: '',
        dest: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入交叉名称', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        source: [
          { required: true, message: '请选择源CTP', trigger: 'change' }
        ],
        dest: [
          { required: true, message: '请选择目的CTP', trigger: 'change' }
        ],

      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>