<template>
  <div class="config_div">
    <el-header style="background-color: #bbf55d">LAG 配置</el-header>
    <el-form :model="lag" :rules="rules" ref="ruleForm" label-width="100px" class="config_form">
      <el-form-item label="链路聚合" prop="name">
        <el-input v-model="lag.name"></el-input>
      </el-form-item>
      <el-form-item label="槽位" prop="slot">
        <el-select v-model="lag.slot" clearable placeholder="请选择槽位">
          <el-option label="slot=1" value="1"></el-option>
          <el-option label="slot=2/3" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="端口" prop="port">
        <el-select v-model="lag.port" clearable placeholder="请选择端口">
          <el-option label="port=1/2" value="3"></el-option>
          <el-option label="port=2/3" value="4"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">确认</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  name:'LAGConfig',
  data() {
    return {
      lag: {
        name: '',
        slot: '',
        port: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入链路聚合名称', trigger: 'blur' },
        ],
        slot: [
          { required: true, message: '请选择槽位', trigger: 'change' }
        ],
        port: [
          { required: true, message: '请选择端口', trigger: 'change' }
        ],

      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>