<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>设备属性</span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="getMe">刷新</el-button>
      </div>
      <div class="text item">名称： {{ me.name }}</div>
      <div class="text item">厂商： {{ me.manufacturer }}</div>
      <div class="text item">设备状态： {{ me.status }}</div>
      <div class="text item">ntp状态： {{ me["ntp-enable"] }}</div>
      <div class="text item">类型： {{ me["device-type"] }}</div>
      <div class="text item">产品名称： {{ me["product-name"] }}</div>
      <div class="text item">uuid： {{ me.uuid }}</div>
      <div class="text item">ip： {{ me["ip-address"] }}</div>
      <div class="text item">掩码： {{ me.mask }}</div>
      <div class="text item">way2： {{ me["gate-way2"] }}</div>
      <div class="text item">硬件版本： {{ me["hardware-version"] }}</div>
      <div class="text item">软件版本： {{ me["software-version"] }}</div>
    </el-card>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Me",
  props: ['name'],
  data() {
    return {
      isMe: false,
      me: {
        "hardware-version": "pvb 1.2.3.6",
        "ntp-enable": false,
        "eq": [
          "EQ=/shelf=1/slot=1/subslot=1/EQ=1",
          "EQ=/shelf=1/slot=1/subslot=1/EQ=2",
          "EQ=/shelf=1/slot=1/subslot=1/EQ=3",
          "EQ=/shelf=1/slot=1/subslot=1/EQ=4",
        ],
        "uuid": "22-3d-12-88",
        "manufacturer": "山东华辰泰尔",
        "software-version": "v1.0.0.0.4",
        "ip-address": "192.168.12.112",
        "layer-protocol-name": [
          "acc-eth:ETH"
        ],
        "device-type": "",
        "name": "",
        "ntp-servers": {
          "ntp-server": [
            {
              "port": 0,
              "name": "hctel"
            }
          ]
        },
        "product-name": "山东华辰泰尔",
        "gate-way2": "192.168.1.1",
        "status": "运行中",
        "mask": "255.255.0.0"
      },
    }
  },
  methods: {
    getMe() {
      if (this.name !== '') {
        axios.get("/node/" + this.name + "/me").then(
            response => {
              console.log("请求成功", response.data);
              this.$message.success("获取设备信息成功");
              this.me = response.data;
            },
            error => {
              console.log("失败！", error.message);
              this.$message.error(error.message);
            }
        )
      } else {
        this.$message.warning("请选择节点后再进行操作 ！")
      }

    },
  }
}
</script>

<style scoped>

</style>