            v-model="pg.primaryPort"
            :options="options"
            @change="handleChange"></el-cascader>-->
            <!--:props="{ expandTrigger: 'hover' }"-->
        <!--      <el-input v-model="pg.primaryPort" placeholder="CTP/PTP"></el-input>-->
      </el-form-item>
      <el-form-item label="备用端口" prop="secondaryPort">
