<script>
export default {
  name: "PGConfig",
  data() {
    return {
      pg: {
        id: '',
        protectionType: 'OCH1+1',
        switchType: '',
        primaryPort: '',
        secondaryPort: '',
        holdOffTime: '',
        mode: ''
      },
      rules: {
        id: [
          {required: true, message: '请输入保护组名称', trigger: 'blur'},
          {type: 'number', message: '保护id必须为数字值，且最大65535',trigger: 'blur'}
        ],
        protectionType: [
          {required: true, message: '请选择保护类型', trigger: 'change'}
