        ],
        type: [
          {type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change'}
        ],
        holdOffTime: [
          {required: true, message: '请输入延迟时间', trigger: 'blur'},
          {type: 'number', message: '延迟时间必须为数字值'}
        ]
      },
      options: [
          {
        value: '1',
        label: 'CTP-1',
        children: [{
            value: 'P1',
            label: 'PTP-1-1'
          },
          {
            value: 'P2',
            label: 'PTP-1-2'
          },
          {
            value: 'P3',
            label: 'PTP-1-3'
          }]
        },
        {
          value: '2',
          label: 'CTP-2',
          children: [
