              label: 'PTP-2-1'
            },
            {
              value: 'P2',
              label: 'PTP-2-2'
            }, {
              value: 'P3',
              label: 'PTP-2-3'
            }]
        },
        {
          value: '3',
          label: 'CTP-3',
          children: [{
              value: 'P1',
              label: 'PTP-3-1'
            },
            {
              value: 'P2',
              label: 'PTP-3-2'
            },
