    onSubmit() {
      console.log('submit!');
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.$bus.$emit('pg:config:visible',false);
    },
    handleChange(value) {
      console.log(value);
    }
  }
}
