<template>
  <el-tabs type="border-card" :active-name="activeName">
    <el-tab-pane label="基本信息" name="first">
      <div style="width: 400px">
        <el-descriptions
            class="margin-top"
            title="设备信息列表"
            :column="1"
            border>
          <template slot="extra">
            <el-button type="primary" size="small" @click="getMe">刷新</el-button>
          </template>
          <el-descriptions-item>
            <template slot="label">
              设备名称
            </template>
            {{ me.name }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">
              uuid
            </template>
            {{ me.uuid }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">
              厂商名称
            </template>
            {{ me.manufacturer }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">
              产品名称
            </template>
            {{ me["product-name"] }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">
              软件版本
            </template>
            {{ me["software-version"] }}
          </el-descriptions-item>

          <el-descriptions-item>
            <template slot="label">
              硬件版本
            </template>
            {{ me["hardware-version"] }}
          </el-descriptions-item>

          <el-descriptions-item>
            <template slot="label">
              设备类型
            </template>
            {{ me["device-type"] }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">
              支持协议
            </template>
            <div v-for="(i,key) in me['layer-protocol-name']" :key="key">
              <span style="color: #0038f5">{{ i }}</span>
<!--              <el-divider direction="vertical" v-if="key!==me['layer-protocol-name'].length-1"></el-divider>-->
            </div>
          </el-descriptions-item>

          <el-descriptions-item>
            <template slot="label">
              运行状态
            </template>
            {{ me.status }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">
              IP
            </template>
            {{ me["ip-address"] }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">
              子网掩码
            </template>
            {{ me.mask }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">
              网关1
            </template>
            {{ me["gate-way1"] }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">
              网关2
            </template>
            {{ me["gate-way2"] }}
          </el-descriptions-item>


          <el-descriptions-item>
            <template slot="label">
              ntp状态
            </template>
            <el-switch
                v-model="me['ntp-enable']"
                active-color="#13ce66"
                inactive-color="#ff4949"
                @change="isNtp">
            </el-switch>
          </el-descriptions-item>


        </el-descriptions>
      </div>
    </el-tab-pane>
    <el-tab-pane label="Ntp" name="second">
      <Ntp/>
    </el-tab-pane>

  </el-tabs>
</template>

<script>
import Ntp from "@/components/property/me/Ntp";
import axios from "axios";

export default {
  name: "NetworkElement",
  components: {
    Ntp
  },
  data() {
    return {
      activeName: 'first',

      nodeName: '',
      me: {
        "hardware-version": "pvb 1.2.3.6",
        "ntp-enable": false,
        "eq": [
          "EQ=/shelf=1/slot=1/subslot=1/EQ=1",
          "EQ=/shelf=1/slot=1/subslot=1/EQ=2",
          "EQ=/shelf=1/slot=1/subslot=1/EQ=3",
          "EQ=/shelf=1/slot=1/subslot=1/EQ=4",
        ],
        "uuid": "22-3d-12-88",
        "manufacturer": "山东华辰泰尔",
        "software-version": "v1.0.0.0.4",
        "ip-address": "192.168.12.112",
        "layer-protocol-name": [
          "ETH", "SDH", "ODU"
        ],
        "device-type": "OTN A3",
        "name": "otna",
        "ntp-servers": {
          "ntp-server": [
            {
              "port": 0,
              "name": "hctel"
            }
          ]
        },
        "product-name": "OTN设备",
        "gate-way1": "192.168.1.1",
        "gate-way2": "192.168.1.2",
        "status": "运行中",
        "mask": "255.255.0.0"
      },
    }
  },
  methods: {
    isNtp() {
      if (this.me["ntp-enable"]) {
        this.activeName = "second";
      } else {
        this.activeName = "first";
      }
    },
    getMe() {
      if (this.nodeName !== '') {
        axios.get("/node/" + this.nodeName + "/me").then(
            response => {
              console.log("请求成功", response.data);
              this.$message.success("获取设备信息成功");
              this.me = response.data;
            },
            error => {
              console.log("失败！", error.message);
              this.$message.error(error.message);
            }
        )
      } else {
        this.$message.warning("请选择节点后再进行操作 ！")
      }

    },
  },
  mounted() {
    this.$bus.$on('node-name', (data) => {
      console.log("收到的信息为： ", data)
      this.nodeName = data;
    })
  }


}
</script>

<style scoped>

</style>