<template>
  <div>
    <el-descriptions
        class="margin-top"
        title="设备信息列表"
        :column="3"
        border
        v-loading="loading"
    >
      <template slot="extra">
        <el-button type="primary" size="small" @click="getInfo">刷新</el-button>
      </template>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-user"></i>
          设备名称
        </template>
        {{ listInfos.name }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-link"></i>
          ip-地址
        </template>
        {{ listInfos["ip-address"] }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-turn-off"></i>
          运行状态
        </template>
        {{ listInfos.status }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-tickets"></i>
          子网掩码
        </template>
        {{ listInfos.mask }}
        <!--        <el-tag size="small">学校</el-tag>-->
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-turn-off"></i>
          ntp状态
        </template>
        <el-switch
            v-model="listInfos['ntp-enable']"
            active-color="#13ce66"
            inactive-color="#ff4949">
        </el-switch>
        <!--        {{ listInfos["ntp-enable"] }}-->
      </el-descriptions-item>
    </el-descriptions>

    <el-button @click="getRpcs">rpcStatistics</el-button>
    <div id="rpcStatistics" ref="rpcStatistics"></div>

    <div>
      <el-input type="textarea" v-model="cacheStr">

      </el-input>
      <el-button @click="sendJson">send for eth connection</el-button>
      <el-button @click="sendJson1">send for put hclink</el-button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import * as echarts from "echarts";

export default {
  data() {
    return {
      cacheStr:'',
      listInfos: {
        name: '',
        'ip-address': '',
        status: '',
        mask: '',
        'ntp-enable': '',
        'gate-way1': '',
        gateWay2: ''
      },
      rpcs: {
        "dropped-sessions": 0,
        // "in-bad-hellos": 0,
        "in-bad-rpcs": 0,
        "in-rpcs": 0,
        "in-sessions": 0,
        // "netconf-start-time": "2022-04-06T10:00:08+02:00",
        "out-notifications": 0,
        "out-rpc-errors": 0
      },

      loading: false
    }
  },
  mounted() {
    let charts = echarts.init(this.$refs.rpcStatistics);
    /*rpc错误总数：0
    打招呼错误总数：0
    通知输出总数：220
    所有rpc总数：3786
    删除会话总数：110
    内置会话总数：113
    南向开始时间：2022-04-06T10:00:08+02:00
    rpc错误总数：0*/
    let xData = ["删除会话总数", "rpc错误总数","所有rpc总数", "内置会话总数","通知输出总数","输出rpc错误"];
    let option = {
      title: {
        text: 'rpc统计图表',
        x: "center",
      },
      xAxis: {
        data: xData,
        type: "category",//坐标轴的类型：value 数据轴  category 类目轴
      },
      yAxis: {

      },
      series: {
        name: "rpc统计",
        type: "bar",//图表类型
        data: [110,0,3786,113,220,0,0]
      }

    }

    charts.setOption(option);
  },

  methods: {
    async sendJson() {
      console.log(JSON.parse(this.cacheStr));
      console.log(this.cacheStr);
      const {data: res} = await this.$http.post("/test/rpc", JSON.parse(this.cacheStr));
      console.log("返回结果",res);

    },
    async sendJson1() {
      console.log(JSON.parse(this.cacheStr));
      console.log(this.cacheStr);
      const {data: res} = await this.$http.post("/test/create/node", JSON.parse(this.cacheStr));
      console.log("返回结果",res);

    },
