      // console.log("收到的数据",res.data);
      //this.listInfos = res.data;

      console.log(localStorage.getItem('authorization'))
      axios.get('/node/me').then(
          response => {
            console.log("请求成功", response.data);
            this.listInfos = response.data;
            this.loading = false;

            // var jsonObj = JSON.parse(JSON.stringify(response.data.data));
            // console.log("@",jsonObj);
            // for (var i =0;i<jsonObj.length;i++){
            //   console.log(jsonObj[i])
            // }
          },
          error => {
            this.loading = true;
            console.log("失败！", error.message)
          }
      )
      //配置连接odl
      // axios.get('http://localhost:8080/restconf/config/network-topology:network-topology/topology/topology-netconf/node/hclink/yang-ext:mount/acc-devm:me', {
      //   // params: {
      //   //   参数
      //   // },
      //   headers: {
      //     'Authorization': "Basic" + localStorage.getItem('authorization')  //这里是关键,设置好Authorization,将定义的token放在后面即可
      //   }
      // }).then(
      //     response => {
      //       console.log("请求成功",response.data)
      //     },
      //     error => {
      //       console.log("失败了",error.messageData)
      //     }
      // );
      // eslint-disable-next-line no-undef
      // axios.get("http://localhost:8080/restconf/config/network-topology:network-topology/topology/topology-netconf/node/hclink/yang-ext:mount/acc-devm:me","admin:admin").then(
      //     response => {
      //       console.log("请求成功",response.data)
      //     },
      //     error => {
      //       console.log("失败了",error.messageData)
      //     }
      // )
      //const {data: res} = await this.$http.post("config/network-topology:network-topology/topology/topology-netconf/node/hclink/yang-ext:mount/acc-devm:me\"");
      //console.log("$$$$: "+res)
    },
    async getRpcs() {
      axios.get('/node/statistics').then(
          response => {
            // console.log("请求成功", response.data);
            this.rpcs = response.data;
            console.log(this.rpcs)
          },
          error => {
            console.log("失败！", error.message)
          }
      )
    },


  },
  // created() {//创建时间节点
  //   console.log('组件创建成功');
  //   let token = '36cb0924-9d82-4f27-a857-62db8474dd57';
  //   // 设置cookie默认过期时间单位是1d(1天)
  //   this.$cookie.set('token', token, 1);
  // },
}
</script>


<style>
.margin-top {
  line-height: 30px;
}

#rpcStatistics {
  width: 800px;
  height: 500px;
  border: 1px solid red;
}
</style>