<template>
  <div class="config_vc_ctp">
<!--    <el-header style="background-color: #1bdb35">VC CTP 配置</el-header>-->
    <el-form :model="vc" ref="ruleForm" label-width="150px" class="config_form">

      <el-form-item label="业务类型" prop="adaptationType">
        <el-select v-model="vc.vcType" clearable
                   placeholder="请选择类型" class="read_input">
          <el-option
              v-for="(i,index) in xc_ctpSignalType"
              :key="index"
              :value="i">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="时隙">
        <el-input v-model="vc.mappingPath"></el-input>
      </el-form-item>

      <el-form-item label="J1 实际发送开销">
        <el-input v-model="vc.J1ActualTx"></el-input>
      </el-form-item>
      <el-form-item label="J1 期望接收开销">
        <el-input v-model="vc.J1ExpectedRx"></el-input>
      </el-form-item>
      <el-form-item label="J1 实际接收开销">
        <el-input v-model="vc.J1ActualRx" readonly class="read_input"></el-input>
      </el-form-item>

      <el-form-item>
        <el-image
            style="width: 20px; height: 25px;"
            :src="require('@/assets/union_rule_font.jpg')"
            :preview-src-list="[require('@/assets/union/vcCtpPac.jpg')]">
        </el-image>
        <el-button type="primary" @click="submitForm('ruleForm')">确认</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {xc_ctpSignalType} from "@/assets/config/XcPac";

export default {
  name: "VcCtp",
  data() {
    return {
      xc_ctpSignalType,
      vc: {
        vcType: '',
        mappingPath: '',
        J1ActualTx: '',
        J1ExpectedRx: '',
        J1ActualRx: ''
      },

    }
  }
}
</script>

<style scoped>
/*<!--使用css样式穿透,input框的样式就没了-->*/
.read_input >>> .el-input__inner {
  border-top: none !important;
  border-left: none !important;
  border-right: none !important;
  border-bottom: none;
  border-radius: 0;
}
</style>