<template>
  <div class="main_p">
    <div class="set_tcas">
      <h3>设置性能参数</h3>
      <el-form :model="setTca" label-width="100px" class="demo-ruleForm">
        <el-form-item label="产生对象" prop="node">
          <el-input v-model="setTca['object-name']" placeholder="请输入产生TCA对象标识"></el-input>
        </el-form-item>
        <el-form-item label="对象类型">
          <el-select v-model="setTca['object-type']" placeholder="请选择事件产生对象类型"
                     class="select_devm">
            <el-option label="网元" value="ME"></el-option>
            <el-option label="单板" value="EQ"></el-option>
            <el-option label="端口" value="PTP"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="性能参数">
          <el-cascader v-model="setTca['pm-parameter-name']" :options="options"
                       :show-all-levels="false"></el-cascader>
        </el-form-item>
        <el-form-item label="周期类型">
          <el-select v-model="setTca.granularity" placeholder="请选择周期类型">
            <el-option
                v-for="(i,index) in granularity"
                :key="index"
                :value="i">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="性能门限值">
          <el-input type="number" v-model="setTca['threshold-value']" placeholder="请输入性能门限值">
            <i slot="suffix" style="margin-right: 10px;">ms</i>
          </el-input>
        </el-form-item>
        <el-form-item label="高/低门限">
          <el-select v-model="setTca['threshold-type']" placeholder="请选择高低门限标识">
            <el-option label="高" value="HIGH"></el-option>
            <el-option label="低" value="LOW"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary">确认</el-button>
          <el-button>重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="get_tcas">
      <h3>查询性能越限阈值</h3>
      <el-form :model="setTca" label-width="100px" class="demo-ruleForm">
        <el-row>
          <el-col :span="6">
            <el-form-item label="对象名称">
              <el-input v-model="getTca.objectName" placeholder="性能监控对象标识"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="周期类型">
              <el-select v-model="getTca.granularity" placeholder="请选择周期类型">
                <el-option
                    v-for="(i,index) in granularity"
                    :key="index"
                    :value="i">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="性能参数">
              <el-cascader v-model="getTca.pmParameterName" :options="options"
                           :show-all-levels="false" clearable></el-cascader>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label-width="0">
              <el-button type="primary">确认</el-button>
              <el-button>重置</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
