          </el-table-column>
          <el-table-column
              prop="threshold-value"
              label="最小值"
              width="80">
          </el-table-column>
          <el-table-column
              prop="threshold-value"
              label="平均值"
              width="80">
          </el-table-column>
          <el-table-column
              prop="threshold-value"
              label="当前值"
              width="80">
          </el-table-column>
        </el-table-column>

      </el-table>

