<template>
  <div class="left-container">
    <el-input
        class="filter-input"
        placeholder="输入关键字进行过滤"
        v-model="filterText"
    >
    </el-input>
    <!-- draggable
        :allow-drag="true"
        :allow-drop="true" -->
    <el-tree
        class="filter-tree"
        :data="data"
        :props="defaultProps"
        :default-expand-all="true"
        :highlight-current="true"
        :expand-on-click-node="false"
        :filter-node-method="filterNode"
        @node-click="handleNodeClick"
        @node-contextmenu="rightClick"
        @getNodeList="getNodeList"
        ref="tree"
    >
    </el-tree>

    <vue-context-menu :contextMenuData="contextMenuData"
                      @newNe="onNewNe"
                      @devm="toDevm"
                      @eqs="toEqs"
                      @xcPac="toXcPac"
                      @pgPac="toPgPac"
                      @lagPac="toLagPac"
                      @alarmMask="toAlarmMask"
                      @tcas="toTcas"
                      @huntMe="onHuntMe"
                      @upload="onUpload"
                      @download="onDownload"
                      @syncC="onSyncC"
                      @syncH="onSyncH"

                      @eq="toEq"
                      @ptps="toPtps"
                      @currentPerformance="toCurrentPerformance"
                      @pastPerformance="toPastPerformance"

                      @ptp="toPtp"
                      @opts="toOpts"
                      @ctps="toCtps">
    </vue-context-menu>
    <!--    <vue-context-menu :contextMenuData="contextMenuData" @savedata="savedata" @newdata="newdata"></vue-context-menu>-->
    <!--    <vue-context-menu :contextMenuData="contextMenuData" @savedata="savedata" @newdata="newdata"></vue-context-menu>-->

    <div v-show="false">
      <ul id="menu" class="menu">
        <li class="menu__item" @click="append(clickNode)">平级添加</li>
        <li class="menu__item" @click="addCard(clickNode)">下级添加</li>
        <li class="menu__item" @click="remove(clickNode)">删除</li>
      </ul>
    </div>


    <div>
      <!--  所有弹窗    -->
      <!--  网元：创建网元   -->
      <el-dialog
          title="创建网元"
          :visible.sync="visibleNewNe"
          width="500px"
          :close-on-click-modal="false">
        <div slot="title" class="header-title" :style="{'background':'#2b7ffd','color':'#fff'}">
          <div style="padding:15px 20px;">创建网元</div>
        </div>
        <DevmInfo :name="clickNode"></DevmInfo>
      </el-dialog>
      <!--  网元：搜索配置   -->
      <el-dialog
          :visible.sync="visibleMatch"
          width="800px"
          :close-on-click-modal="true"
          :destroy-on-close="true"
          :show-close="false"
          class="match_config_dialog">
        <div slot="title" class="header-title" :style="{'background':'#2b7ffd','color':'#fff'}">
          <div style="padding:15px 20px;">{{ huntTitle }}</div>
        </div>
        <MatchConfig/>
      </el-dialog>
      <!--  网元：网元配置上传   -->
      <el-dialog
          :visible.sync="visibleUpload"
          width="460px"
          :close-on-click-modal="true"
          :destroy-on-close="true"
          :show-close="false"
          class="match_config_dialog">
        <div slot="title" class="header-title" :style="{'background':'#2b7ffd','color':'#fff'}">
          <div style="padding:15px 20px;">{{ uploadTitle }}</div>
        </div>
        <UploadFtpFile/>
      </el-dialog>
      <!--  网元：网元配置下载   -->
      <el-dialog
          :visible.sync="visibleDownload"
          width="460px"
          :close-on-click-modal="true"
          :destroy-on-close="true"
          :show-close="false"
          class="match_config_dialog">
        <div slot="title" class="header-title" :style="{'background':'#2b7ffd','color':'#fff'}">
          <div style="padding:15px 20px;">{{ downloadTitle }}</div>
        </div>
        <DownloadFtpFile/>
      </el-dialog>
      <!--  网元：同步当前告警   -->
      <el-dialog
          title="同步当前告警"
          :visible.sync="visibleSyncAlarmC"
          width="380px"
          :close-on-click-modal="false">
        <div slot="title" class="header-title" :style="{'background':'#2b7ffd','color':'#fff'}">
          <div style="padding:15px 20px;">同步当前告警</div>
        </div>
        <TipWindows/>
      </el-dialog>
      <!--  网元：同步历史告警   -->
      <el-dialog
          title="同步历史告警"
          :visible.sync="visibleSyncAlarmH"
          width="380px"
          :close-on-click-modal="false">
        <div slot="title" class="header-title" :style="{'background':'#2b7ffd','color':'#fff'}">
          <div style="padding:15px 20px;">同步历史告警</div>
        </div>
        <TipWindows/>

      </el-dialog>
    </div>
  </div>
</template>


<script>
import axios from "axios";
import {treeData} from "@/assets/config/OosViewData";
import DevmInfo from "@/components/DevmInfo";
import TipWindows from "@/components/Tip/TipWindows";
import MatchConfig from "@/components/Tip/MatchConfig";
import UploadFtpFile from "@/components/rms/UploadFtpFile";
import DownloadFtpFile from "@/components/rms/DownloadFtpFile";

export default {
  components: {DevmInfo, MatchConfig, TipWindows, UploadFtpFile, DownloadFtpFile},
  data() {
    return {
      //对话框
      //创建设备
      visibleNewNe: false,
      //搜索配置
      visibleMatch: false,
      //网元配置上传
      visibleUpload: false,
      //网元配置下载
      visibleDownload: false,
      //同步当前告警
      visibleSyncAlarmC: false,
      //同步历史告警
      visibleSyncAlarmH: false,

      //对话框标题名称
      //搜索
      huntTitle: '',
      //网元配置上传
      uploadTitle: '',
      //网元配置下载
      downloadTitle: '',

      //左侧树点击的网元名称
      clickNode: "",
      //全局事件总线发送的数据
      node:'',
      slot:'',
      ptp:'',
      //树节点的点击次数
      clickCount: 0,
      //定时器
      timer: '',

      menuVisible: false,

      id: 14,
      filterText: "",
      iconStyle: 'el-icon-s-grid',
      //左侧树的所有信息
      data: treeData,
      defaultProps: {
        children: "children",
        label: "label",
      },


      // Menu options (菜单选项)
      //全局视图
      globalMenu: [
        {
          fnHandler: "newNe",
          icoName: "el-icon-edit-outline",
          btnName: "创建网元"
        },
      ],

      //设备
      meMenu: [
        {
          fnHandler: "devm",
          icoName: "el-icon-s-ticket",
          btnName: "设备属性"
        },
        {
          fnHandler: "eqs",
          icoName: "el-icon-cpu",
          btnName: "板卡信息"
        },
        {
          fnHandler: "xcPac",
          icoName: "el-icon-share",
          btnName: "交叉配置"
        },
        {
          fnHandler: "pgPac",
          icoName: "el-icon-menu",
          btnName: "保护组管理"
        },
        {
          fnHandler: "lagPac",
          icoName: "el-icon-picture-outline-round",
          btnName: "LAG保护"
        },
        {
          fnHandler: "alarmMask",
          icoName: "el-icon-close-notification",
          btnName: "告警抑制配置"
        },
        {
          fnHandler: "tcas",
          icoName: "el-icon-takeaway-box",
          btnName: "性能门限"
        },
        {
          fnHandler: "huntMe",
          icoName: "el-icon-search",
          btnName: "搜索配置"
        },
        {
          fnHandler: "upload",
          icoName: "el-icon-upload2",
          btnName: "网元配置上传"
        },
        {
          fnHandler: "download",
          icoName: "el-icon-download",
          btnName: "网元配置下载"
        },
        {
          fnHandler: "syncC",
          icoName: "el-icon-video-pause",
          btnName: "同步当前告警"
        },
        {
          fnHandler: "syncH",
          icoName: "el-icon-video-play",
          btnName: "同步历史告警"
        },

      ],

      //单板
      eqMenu: [
        {
          fnHandler: "eq",        // Binding events(绑定事件)
          icoName: "el-icon-cpu",      // icon (icon图标 )
          btnName: "单板属性"             // The name of the menu option (菜单名称)
        },
        {
          fnHandler: "ptps",
          icoName: "el-icon-set-up",
          btnName: "端口批量配置"
        },
        {
          fnHandler: "alarmMask",
          icoName: "el-icon-close-notification",
          btnName: "告警抑制配置"
        },
        {
          fnHandler: "tcas",
          icoName: "el-icon-takeaway-box",
          btnName: "性能门限"
        },
        {
          fnHandler: "currentPerformance",
          icoName: "el-icon-data-line",
          btnName: "当前性能"
        },
        {
          fnHandler: "pastPerformance",
          icoName: "el-icon-data-board",
          btnName: "历史性能"
        },
      ],

      //端口
      ptpMenu: [
        {
          fnHandler: "ptp",
          icoName: "el-icon-s-tools",
          btnName: "端口属性"
        },
        {
          fnHandler: "opts",
          icoName: "el-icon-aim",
          btnName: "光模块"
        },
        {
          fnHandler: "ctps",
          icoName: "el-icon-connection",
          btnName: "CTP资源"
        },

        {
          fnHandler: "alarmMask",
          icoName: "el-icon-close-notification",
          btnName: "告警抑制配置"
        },
        {
          fnHandler: "tcas",
          icoName: "el-icon-takeaway-box",
          btnName: "性能门限"
        },
        {
          fnHandler: "currentPerformance",
          icoName: "el-icon-data-line",
          btnName: "当前性能"
        },
        {
          fnHandler: "pastPerformance",
          icoName: "el-icon-data-board",
          btnName: "历史性能"
        },

      ],

      // contextmenu data (菜单数据)
      contextMenuData: {
        // the contextmenu name(@1.4.1 updated)
        menuName: "demo",
        // The coordinates of the display(菜单显示的位置)
        axis: {
          x: null,
          y: null
        },
        // Menu options (菜单选项)
        menulists: [
          {
            fnHandler: "savedata",        // Binding events(绑定事件)
            icoName: "el-icon-crop",      // icon (icon图标 )
            btnName: "单板属性"             // The name of the menu option (菜单名称)
          },
          {
            fnHandler: "newdata",
            icoName: "fa fa-home fa-fw",
            btnName: "端口批量配置"
          },
          {
            fnHandler: "newdata",
            icoName: "fa fa-home fa-fw",
            btnName: "告警抑制配置"
          },
          {
            fnHandler: "newdata",
            icoName: "fa fa-home fa-fw",
            btnName: "性能门限"
          },
          {
            fnHandler: "newdata",
            icoName: "fa fa-home fa-fw",
            btnName: "当前性能"
          },
          {
            fnHandler: "newdata",
            icoName: "fa fa-home fa-fw",
            btnName: "历史性能"
          },
        ]
      }

    };
  },
  methods: {
    //300毫秒的双击
    isDbClickOnTree() {
      this.clickCount++;
      if (this.clickCount > 2) {
        return
      }
      this.timer = window.setTimeout(() => {
        if (this.clickCount === 2) {
          this.clickCount = 0;
          this.$router.push('/oos/ne_center/demo');
        } else {
          this.clickCount = 0;
        }
      }, 300)
    },

    //左侧树点击事件
    handleNodeClick(data, e) {
      console.log(this);
      console.log("展示所点击的节点信息", data);
      console.log(data.value);
      console.log('我所点击获取的e元素是：', e);
      switch (data.value) {
        case 0:
          this.$bus.$emit("global:view", e.data.children);
          console.log('全局视图下 获取的e元素是：', e.data.children);
          this.$bus.$emit("level", data.value);
          break;
        case 1:
          //网元
          this.$bus.$emit("node-name", data.label);
          this.$bus.$emit("node:eqs", data.children[0].children);
          console.log("node:eqs", data.children[0].children);
          this.$bus.$emit("level", data.value);
          this.isDbClickOnTree();
          break;
        case 2:
          //机架
          console.log('当我为机架时，我还是传网元信息:', e.parent.data.label);
          this.$nextTick(function () {
            this.$bus.$emit("node-name", e.parent.data.label);
            this.$bus.$emit("node:eqs", data.children);
            this.$bus.$emit("level", data.value);
          });
          this.isDbClickOnTree();
          break;
        case 3:
          this.$bus.$emit("node-name", e.parent.parent.data.label);
          this.$bus.$emit("slot-name", data.label);
          console.log('槽位信息:', data.label);
          this.$bus.$emit("node:eqs:ptps", data.children);
          this.$bus.$emit("level", data.value);
          break;
        case 4:
          debugger
          this.$bus.$emit("node-name", e.parent.parent.parent.data.label);
          console.log('ptp-node:', e.parent.parent.parent.data.label);
          this.$bus.$emit("slot-name", e.parent.data.label);
          console.log('ptp-slot:', e.parent.data.label);
          this.$bus.$emit("ptp-name", data.label);
          console.log('端口信息:', data.label);
          this.$bus.$emit("level", data.value);


          break;
      }

      // if (data.label !== "全局视图") {
      //   if (data.value !== 2) {
      //     console.log(data.label);
      //     this.$nextTick(function () {
      //       this.$bus.$emit("node-name", data.label);
      //       this.$bus.$emit("level", data.value);
      //     })
      //   }
      //   if (data.value === 2) {
      //     console.log('当我为机架时，我还是传网元信息:', e.parent.data.label);
      //     this.$nextTick(function () {
      //       this.$bus.$emit("node-name", e.parent.data.label);
      //       this.$bus.$emit("level", data.value);
      //     })
      //   }
      //
      // }
    },
    //过滤树节点
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    showMenu(event) {
      event.preventDefault();
      var x = event.clientX;
      var y = event.clientY;
      // Get the current location
      this.contextMenuData.axis = {
        x,
        y
      };
    },
    //右键事件
    rightClick(MouseEvent, object, Node, element) {
      // 鼠标右击触发事件
      this.menuVisible = false; // 先把模态框关死，目的是 第二次或者第n次右键鼠标的时候 它默认的是true
      this.menuVisible = true; // 显示模态窗口，跳出自定义菜单栏
      let menu = document.querySelector("#menu");
      // this.showMenu();

      switch (Node.level) {
        case 1:
          //全局视图
          this.contextMenuData.menulists = this.globalMenu;
          break;
        case 2:
          //网元
          //获取所点击的节点名称，下同
          this.clickNode = Node.data.label;
          this.huntTitle = '搜索设备配置信息：' + this.clickNode;
          this.uploadTitle = '网元配置上传：' + this.clickNode;
          this.downloadTitle = '网元配置下载：' + this.clickNode;
          this.contextMenuData.menulists = this.meMenu;
          this.$bus.$emit("node-name", this.clickNode);

          break;
        case 3:
          //机架
          this.clickNode = Node.parent.data.label;
          this.huntTitle = '搜索设备配置信息：' + this.clickNode;
          this.uploadTitle = '网元配置上传：' + this.clickNode;
          this.downloadTitle = '网元配置下载：' + this.clickNode;
          this.contextMenuData.menulists = this.meMenu;
          this.$bus.$emit("node-name", this.clickNode);

          break;
        case 4:
          //槽道（单板）
          this.contextMenuData.menulists = this.eqMenu;
          break;
        case 5:
          //端口
          this.contextMenuData.menulists = this.ptpMenu;
          this.$bus.$emit('ptp-name',Node.data.label);
          break;
        default:
          break;
      }

      MouseEvent.preventDefault();
      let x = MouseEvent.clientX;
      let y = MouseEvent.clientY;
      // Get the current location
      this.contextMenuData.axis = {
        x,
        y
      };

      menu.style.left = MouseEvent.clientX + 5 + "px"; // MouseEvent.clientX获取鼠标点击的坐标，在该处新增加的menu填充的位置
      menu.style.top = MouseEvent.clientY - 10 + "px";
      document.addEventListener("click", this.foo); // 给整个document添加监听鼠标事件，点击任何位置执行foo方法
      console.log("右键被点击的event:", MouseEvent);
      console.log("右键被点击的object:", object);
      console.log("右键被点击的value:", Node);
      console.log("右键被点击的element:", element);
      console.log("鼠标点击了树形结构图");
      this.clickNode = Node; //存储待操作的节点或删除、或在该层或子层添加节点
      // alert(Node.level +":"+Node.label)
    },
    foo() {
      // 取消鼠标监听事件 菜单栏
      this.menuVisible = false;
      document.removeEventListener("click", this.foo); // 要及时关掉监听，不关掉的是一个坑，不信你试试，虽然前台显示的时候没有啥毛病，加一个alert你就知道了
    },

    onNewNe() {
      this.visibleNewNe = true;
    },

    toDevm() {
      this.$router.push('/oos/ne_center');

    },
    toEqs() {
      this.$router.push('/oos/ne_center/eqs');
    },

    toXcPac() {
      this.$router.push('/oos/ne_center/me/xcs');
    },
    toPgPac() {
      this.$router.push('/view/pgs');
    },
    toLagPac(){
      this.$router.push('/oos/ne_center/lags');
    },
    toAlarmMask() {
      this.$router.push('/oos/ne_center/alarms&mask');
    },
    toTcas() {
      this.$router.push('/oos/ne_center/tcas');
    },
    onHuntMe() {
      this.visibleMatch = true;
    },
    onUpload() {
      this.visibleUpload = true;
    },
    onDownload() {
      this.visibleDownload = true;
    },
    onSyncC() {
      this.visibleSyncAlarmC = true;
    },
    onSyncH() {
      this.visibleSyncAlarmH = true;
    },
    //单板
    toEq() {
      this.$router.push('/oos/ne_center/eq');
    },
    toPtps() {
      this.$router.push('/oos/ne_center/eqs/ptps');
    },
    toCurrentPerformance() {
      this.$router.push('/oos/ne_center/p/c');
    },
    toPastPerformance() {
      this.$router.push('/oos/ne_center/p/h');
    },
    //端口
    toPtp() {
      this.$router.push('/oos/ne_center/ptps');
    },
    toOpts() {
      this.$router.push('/oos/ne_center/opts');
    },
    toCtps() {
      this.$router.push('/oos/ne_center/basic_ctps');
    },


    addCard(data) {
      const newChild = {id: this.id++, label: "testtest", children: []};
      //debugger
      if (!data.data.children) {
        this.$set(data.data, "children", []); // 最后一层节点添加下一层节点，先给其添加children属性
      }
      data.data.children.push(newChild);
    },
    remove(data) {
      //const newChild = { id: this.id++, label: 'testtest', children: [] };
      //debugger
      if (data.parent.data.children) {
        return data.parent.data.children.pop(data.data); // 第一层以外的节点，父节点有children，从其父节点的children踢去该节点
      }
      data.parent.data.pop(data.data); // 第一层节点 ，父节点没有children从其父节点的children踢去该节点
    },
    append(data) {
      const newChild = {id: this.id++, label: "testtest", children: []};
      //debugger
      if (!data.parent.data.children) {
        return data.parent.data.push(newChild); // 第一层节点添加兄弟层级菜单栏
      }
      data.parent.data.children.push(newChild); // 第二层节点添加兄弟层级菜单栏
    },
    getNodeList() {
      axios.get("/list/nodes").then(
          (response) => {
            console.log("请求成功", response.data);
            // alert("网元数量为："+response.data.length);
            //清空数组，也可直接=[]一样实现清空
            this.data[0].children.splice(0);

            //替换数组中的标签名 由 node-id -> label
            this.data[0].children = response.data.map((item) => {
              return {
                label: item["node-id"],
              };
            });
            this.$bus.$emit("node-info", response.data);
            // console.log(myList);
            // this.data[0].children = myList;
            // for(let i=0;i<response.data.length;i++){
            //
            //   this.data[0].children[i].label = response.data[i]['node-id']
            //   console.log(this.data[0].children)
            // }
            // this.data[0].children = response.data;
          },
          (error) => {
            console.log("失败！", error.message);
            // this.$message.error(error.message);
          }
      );
    },
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
  },
  mounted() {
    this.$bus.$on("update:visible", (data) => {
      this.visibleMatch = data;

    });
    this.$bus.$on("config:visible", (data) => {
      this.visibleUpload = data;
      this.visibleDownload = data;

    });
    this.$bus.$emit('new:ne', (data) => {
      console.log('看看我收到了什么数据，来自创建网元的信息： ', data)
    })

    this.$bus.$on("new-node", (data) => {
      console.log("received new node : ", data);
      this.data[0].children.push({label: data});
    });

    this.$bus.$on("delete-node", (data) => {
      console.log("delete node : ", data);
      for (let index in this.data[0].children) {
        if (this.data[0].children[index].label === data) {
          this.data[0].children.splice(index, 1);
        }
      }
    });
    this.getNodeList();
  },
};
</script>

<style>
/* 左侧样式 */
.left-container {
  height: auto;
  line-height: 30px;
  /*position: absolute;*/
