<template>
  <div>
    <el-tabs tab-position="left" active-name="2">
      <el-tab-pane label="形式1">
        <el-button style="float: right; padding: 3px 0" type="text" @click="getEqs">刷新</el-button>
        <el-table :data="info1.eqs.eq">
          <el-table-column property="name" label="板卡名称" width="300"></el-table-column>
          <el-table-column property="me" label="所属机框"></el-table-column>
          <el-table-column property="slot" label="所属槽位" width="100"></el-table-column>
          <el-table-column width="150" property="plug-state" label="接入状态">
            <template slot-scope="scope">
              <el-tag
                  :type="scope.row['plug-state'] ? 'primary' : 'success'"
                  disable-transitions>{{ scope.row['plug-state'] ? '是' : '否' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column width="150" property="eq-type" label="板卡类型"></el-table-column>
          <el-table-column width="150" property="eq-sn" label="SN码"></el-table-column>
          <el-table-column width="auto" property="software-version" label="软件版本"></el-table-column>
          <el-table-column width="auto" property="hardware-version" label="硬件版本"></el-table-column>
          <el-table-column width="150" property="eq-state" label="运行状态"></el-table-column>
        </el-table>
      </el-tab-pane>

      <el-tab-pane label="形式2" name="2">
        <el-descriptions title="单板属性" :column="1" :border="true" style="width: 800px">
          <template slot="extra">
            <el-button type="primary" size="small">刷新</el-button>
          </template>
          <el-descriptions-item>
            <template slot="label">板卡名称</template>
            {{ info.eq.name }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">所属机框</template>
            {{ info.eq.me }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">所属槽位</template>
            {{ info.eq.slot }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label">在位状态</template>
            <el-tag size="medium" :type="info.eq['plug-state']?'success':'danger'" effect="dark">
              {{ info.eq["plug-state"] ? '已在位' : '无板卡' }}
            </el-tag>
          </el-descriptions-item>

          <el-descriptions-item>
            <template slot="label"> 运行状态</template>
            {{ info.eq["eq-state"] }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label"> SN码</template>
            {{ info.eq["eq-sn"] }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label"> 软件版本</template>
            {{ info.eq["software-version"] }}
          </el-descriptions-item>
          <el-descriptions-item>
            <template slot="label"> 硬件版本</template>
            {{ info.eq["hardware-version"] }}
          </el-descriptions-item>

          <el-descriptions-item>
            <template slot="label"> 包含PTP</template>
            <div v-for="(i,key) in info.eq.ptp" :key="key">
              <p>{{ i }}</p>
              <!--                <el-divider direction="vertical"></el-divider>-->
            </div>
          </el-descriptions-item>
        </el-descriptions>

      </el-tab-pane>

    </el-tabs>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Eq",
  data() {
    return {
      nodeName: '',
      info: {
        eq: {
          name: "EQ=/shelf=1/slot=1/subslot=1/EQ=1",
          "plug-state": true,
          me: 'otna',
          slot: '1',
          "software-version": "v1.0.8  -2020/8/7 16:50:48",
          "hardware-version": "pcb_ver:1 board_type:none",
          'eq-type': 'Power',
          'eq-sn': 'cd12-322344',
          ptp: [
            "端口-1",
            "端口-2",
            "端口-3",
            "端口-4",
            "端口-5",
            "端口-6",
          ],
          "eq-state": "初始化中···"
        },
      },
      info1: {
        eqs: {
          "eq": [
            {
              "name": "槽道-1 OTN2X8",
              "plug-state": true,
              me: 'otna',
              slot: '1',
              "software-version": "v1.0.8  -2020/8/7 16:50:48",
              "hardware-version": "pcb_ver:1 board_type:none",
              'eq-type': 'Power',
              'eq-sn': 'cd12-322344',
              "ptp": [
                "PTP=/shelf=1/slot=1/subslot=1/port=9",
                "PTP=/shelf=1/slot=1/subslot=1/port=8",
                "PTP=/shelf=1/slot=1/subslot=1/port=10",
                "PTP=/shelf=1/slot=1/subslot=1/port=7"
              ],
              "eq-state": "初始化中···"
            },

          ]
        },
      }
    }
  },
  methods: {
    getEqs() {
      if (this.nodeName !== '') {
        axios.get("/node/" + this.nodeName + "/eqs").then(
            response => {
              console.log("请求成功", response.data);
              this.$message.success("获取设备信息成功");
              this.eqs = response.data;
            },
            error => {
              console.log("失败！", error.message);
              this.$message.error(error.message);
            }
        )
      } else {
        this.$message.warning("请选择节点后再进行操作 ！")
      }
    },
  },
  mounted() {
    this.$nextTick(function () {
      this.$bus.$on('node-name', (data) => {
        this.nodeName = data;
        this.info.eq.name = data;
      })
    })

  }

}
</script>

<style scoped>

</style>